
/* src/ProductList.css */
.product-list-container {
    padding: 20px;
    max-width: 500px;
    margin: 0 auto;
    text-align: center;
    background: none;
}

.product-list {
   
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    width: 100%;
    height: auto;
    justify-content: center;
    align-items: center;
}

.product {
    border: 2px solid #007bff;
    border-radius: 5px;
    padding: 10px;
    text-align: center; /* Центрируем текст */
    
 
}

.product-image  {
    

    background-color: rgb(164, 129, 129);
    max-height:400px;
    border-radius: 5px;
    width: 300px; 
    height: 200px; 
    overflow: hidden;
}

button {
    padding: 10px;
    background-color: #51a74a;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

button:hover {
    /* background-color: #51a74a; */
}

.popup {
    position: relative;
    margin: 0 auto;
    max-width: 500px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000000;
}

.popup-content {
    background-color: rgb(164, 129, 129);
    padding: 20px;
    margin: 20px;
    border-radius: 5px;
    position: relative;
    text-align: center; /* Центрируем текст */
}

.close {
    background-color: rgb(164, 129, 129);
    position: absolute;
    top: 10px;
    right: 20px;
    cursor: pointer;
    font-size: 24px;
}

.image-gallery {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
}

.image-gallery img {
    max-width: 100%;
    height: auto;
    margin: 0 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.image-gallery button {
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: #4CAF50; /* Цвет кнопок навигации */
}

.image-gallery button:disabled {
    color: #ccc; /* Цвет кнопок при отключении */
    cursor: not-allowed; /* Указатель при отключении */
}
h1{
    color:#fefe03 ;
    font-size: 130%;
}
.popup-content h2{
    background-color: rgb(164, 129, 129);
}
.strelki{
    height: 100px;
    color: #000;
}

@media (max-width: 600px) {
    .product-list  {
        display: grid;
        grid-template-columns: 1fr ;
        max-width: 100%;
     
    }
    .product-image {
        max-width: 100%; 
        max-height: 100%; 
    }
  }
