.popap{
    margin: 10% auto;
    text-align: center;
 
}
.h2{
    
   color: rgb(190, 252, 4);
   font-size: 80%;
   font-style: italic;
   letter-spacing: 1px;
   font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;


}
.overlay{
    border-radius: 80px;
}
.buttonPop{
    background-color: rgb(0, 8, 255);
    box-shadow: 8px 8px rgba(61, 10, 10, 0.5);
    color:rgb(225, 255, 2);
    animation-name: rotation1;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
    letter-spacing: 1px;
    width: 60%;
}
.kombo{
    color: rgb(191, 107, 4);
    font-size: 120%;
}
.rembo{
    width: 100%;
}
.proz50{
    color: #fff307d6;
}
.paket{
    color: rgba(255, 80, 11, 0.73);
}

