.boxx{
    margin-top: 20px;
}
.buttonHeader{
  
    background-color: #2a2c29;
 
    border: none;
    font-size:83%;
    color: rgb(255, 255, 255);
   
}

.header{
    cursor: pointer;
    max-width: 900px;
    display: grid;
    grid-template-columns: 30% 40% 30%;
    justify-content: center;
    text-align: center;
    margin-bottom: 50px;
  

    
}

.logo{
   
    color: rgb(255, 255, 255);
    text-align: center;
    margin: 0 auto;
    
   
}

p{font-size: 79%; 
    letter-spacing: 2px;
  
    font-weight: bolder;
    background-size:cover ;
   -webkit-background-clip: text;
    color: transparent;

    
}

.buttonHeader img{
    
    animation-name: rotation;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    max-width: 60%;
    cursor: pointer;
    background-color: #2a2c29;
  
}

.sylkiButtton{
    font-size: 100%;
  
    background-color: #2a2c29;
  
    margin:auto;
    
}

p.cheburek{
    text-shadow: 0.5px 0.5px  0.5px rgb(125, 123, 123);
    max-width: 100%;
}
.cheburek{
    color: #908f8f;
    font-family: 'Times New Roman', Times, serif;
    animation-name: rotation1;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
 
    font-size: 150%; 
    margin-bottom: 30px;
    position: relative;
    z-index: 199;
   
}
.buttonHeader1{
    cursor: pointer;
    animation-name: rotation1;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    border: none;
    margin: 0;
    padding: 0;
    background-color: #2a2c29;
   
}
.buttonHeader1:hover{
    background-color: #2a2c29;
}

.buttonHeader:hover{
    background-color: #2a2c29;
}


