.lozung{
    text-align: center;
    font-size: 200%;
    color: rgb(122, 2, 2);
    font-weight: 600;
    text-shadow:   0.8px 0.9px  0.8px rgb(243, 241, 241);
    letter-spacing: 3px;
    font-family: Arial, Helvetica, sans-serif;
    font-style: italic ;
    margin-bottom: 10%;
   

}
.log{
display: flex;
justify-content: center;
margin-bottom: 50px;
}
.logo1{
    width: 20%;

}
.logo{
    background-color: none;
    width: 140px;
}

 @keyframes rotation {
    0% {
        transform:rotate(0deg);
    }
    100% {
        transform:rotate(360deg);
       
    }
}

@keyframes rotation1 {
    0% {
        transform:scale(1);
    }
    100% {
        transform:scale(1.4);
       
      
      
       
    }
}

@media (max-width:500px){
    .cheburek{
        font-size: 0.7rem;
    }
.buttonHeader{
    width: auto;
}
.buttonHeader1{
    width: auto;
}
.cheburek{
  width: auto;  
}
}
